body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

@media screen and (width <= 1024px) {
  header {
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
    display: flex;
  }

  header a.logo {
    align-items: center;
    display: flex;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
  }

  li {
    margin-right: 20px;
  }

  li:first-child {
    margin-left: 0;
  }

  li:last-child {
    margin-right: 0;
  }

  a {
    text-transform: uppercase;
    color: #000;
    align-items: center;
    height: 60px;
    text-decoration: none;
    display: flex;
    position: relative;
  }

  a:hover {
    color: #00acdc;
  }

  a.is-active {
    position: relative;
  }

  a.is-active:after {
    content: "";
    background-color: #00acdc;
    border-radius: 8px;
    width: 100%;
    height: 4px;
    display: block;
    position: absolute;
    bottom: 0;
  }

  a.is-active, a:active {
    color: #00acdc;
    border-bottom: none;
    border-radius: 0;
  }
}

